.map-wrapper {
  background: #fff;
  text-align: center;
  height: 90vh;
  @media only screen and (min-width: 768px)  {
    align-items: center;
    display: flex;
    height: 100vh;
    justify-content: center;
  }

  iframe {
    background: #fff;
    border-radius: 10px;
    height: 90%;
    width: 88%;
    margin: 0 auto;
    border-radius: 5px;
    @media only screen and (min-width: 768px)  {
      //max-width: 95vw;
      max-height: 90vh;
    }
  }
}
